.about {
  &__title {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: #fff;
    margin: 0 0 32px;

    @include onTablet () {
      font-size: 48px;
      
    }

    @include onDesktop () {
      font-size: 64px;

      
    }
    
  }

  &__paragraph {
    font-family: "Inter", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;

    color: #fff;
    margin: 0;
  }
}
